.card {
  position: relative;
  background: #fff;
  border-top: 1px solid #DDE2E6;
  border-bottom: 1px solid #DDE2E6;
  margin: 23px 0 0 0;

  section {
    margin-top: 60px;
    border-top: 1px solid #DDE2E6;
    padding-top: 60px;

    &:first-of-type {
      margin-top: 0;
      border-top: 0;
      padding-top: 0;
    }

    h2 {
      margin-top: 3px;
    }

    form {
      margin-top: 45px;
    }
  }
}

.card-mod {
  background: #fff;
  border: 1px solid #DDE2E6;
  border-top: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  margin: 0 60px;
}

.card__topbar {
  height: 96px;
  border-bottom: 1px solid #DDE2E6;
  padding: 36px 24px;

  h1 {
    @include ellipsis;
  }
}

.card__topbar.card__topbar--with-description {
  height: auto;
  padding: 39px 24px 35px 24px;

  p {
    color: $color-text-primary;
    margin: 9px 0 0 0;
  }
}

.card__nav {
  border-bottom: 1px solid #DDE2E6;
  padding-left: 9px;
  height: 48px;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 0; // Removes space between inline-block elements
    white-space: nowrap;
    overflow: scroll; // Makes long navigation scrollable on smaller viewports
  }

  li {
    display: inline-block;
  }

  a {
    position: relative;
    display: inline-block;
    color: #333;
    font-size: 14px;
    line-height: 24px;
    padding: 12px 15px;
    transition: all 0.2s;

    &:before {
      content: '';
      visibility: hidden;
      opacity: 0;
      background: $color-primary;
      width: calc(100% - 30px);
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 15px;
      transition: all 0.2s;
    }

    &.card__nav-active, &:hover, &:active, &:focus {
      color: $color-primary;

      &:before {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.card__sub {
  position: relative;

  .card__topbar {
    height: 60px;
    padding: 18px 24px;

    h2 {
      @include ellipsis;
      font-size: 16px;
      line-height: 24px;
      margin: 0;
    }
  }
}

.card__header {
  padding: 35px 60px;
  border-bottom: 1px solid #DDE2E6;

  h1 {
    font-weight: 600;
  }
}

.card__content {
  position: relative;
  padding: 48px 24px;

  > *:first-child {
    margin-top: -12px;
  }

  > h1:first-child {
    margin-top: -9px;
  }

  h1 + p {
    margin-top: 15px;
  }

  h3 {
    font-size: 16px;
    font-weight: 600;
    margin: 0 0 24px;
  }

  &__title {
    margin: 0 0 46px !important;
    font-weight: 600;
  }

  .alert {
    padding: 12px 24px;
  }

  .alert-info {
    a {
      color: #31708f;
      font-weight: 500;
      text-decoration: underline;

      &:hover, &:focus, &:active {
        text-decoration: none;
      }
    }
  }
}

.card__empty-state {
  h3 {
    margin: 4px 0 0 0;
  }

  .onboarding-content{
    margin-top: 13px;
  }
}

.card__notice {
  background: #d9edf7;
  color: #31708f;
  border-bottom: 1px solid #DDE2E6;
  padding: 23px 24px;

  a {
    color: #31708f;
    font-weight: 500;
    text-decoration: underline;

    &:hover, &:focus, &:active {
      text-decoration: none;
    }
  }
}

// Larger viewports
@media (min-width: 1024px) {
  .card {
    margin: 59px 60px 0 60px;
    border-radius: 4px;
    border-left: 1px solid #DDE2E6;
    border-right: 1px solid #DDE2E6;

    &.mod {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  .card__topbar {
    padding: 36px 400px 36px 60px;
  }

  .card__topbar.card__topbar--with-description {
    padding: 39px 60px 35px 60px;
  }

  .card__nav {
    position: absolute;
    top: 0;
    right: 45px;
    height: 96px;
    padding-left: 0;

    ul {
      overflow: auto;
    }

    a {
      padding-top: 39px;
      padding-bottom: 33px;
    }
  }

  .card__sub {
    .card__topbar {
      padding: 18px 24px 18px 60px;
    }

    .card__nav {
      height: 60px;

      a {
        padding-top: 19px;
        padding-bottom: 17px;
      }
    }
  }

  .card__content {
    padding: 60px;
    min-height: 168px;
  }

  .card__delete {
    .button, input[type=submit], input[type=submit].button {
      position: absolute;
      bottom: 60px;
      left: 60px;
      margin: 0;
    }
  }

  .card__notice {
    padding: 23px 60px;
  }
}
